import { select, event, mouse } from 'd3-selection';
import { zoom } from 'd3-zoom';
import versor from 'versor';
import Kapsule from 'kapsule';

var geoZoom = Kapsule({
  props: {
    projection: {
      onChange: function onChange(projection, state) {
        state.unityScale = projection ? projection.scale() : 1;
      }
    },
    scaleExtent: {
      default: [0.1, 1e3],
      onChange: function onChange(extent, state) {
        state.zoom && state.zoom.scaleExtent(extent);
      }
    },
    northUp: {
      default: false
    },
    onMove: {
      defaultVal: function defaultVal() {}
    }
  },
  init: function init(nodeEl, state) {
    select(nodeEl).call(state.zoom = zoom().scaleExtent(state.scaleExtent).on('start', zoomStarted).on('zoom', zoomed));
    var v0, r0, q0;

    function zoomStarted() {
      if (!state.projection) return;
      v0 = versor.cartesian(state.projection.invert(mouse(this)));
      r0 = state.projection.rotate();
      q0 = versor(r0);
    }

    function zoomed() {
      if (!state.projection) return;
      var scale = event.transform.k * state.unityScale;
      state.projection.scale(scale);
      var v1 = versor.cartesian(state.projection.rotate(r0).invert(mouse(this))),
          q1 = versor.multiply(q0, versor.delta(v0, v1)),
          rotation = versor.rotation(q1);

      if (state.northUp) {
        rotation[2] = 0; // Don't rotate on Z axis
      }

      state.projection.rotate(rotation);
      state.onMove({
        scale: scale,
        rotation: rotation
      });
    }
  }
});

export default geoZoom;
